.movie-wrap {
  margin: 30px auto;
  width: 900px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}


.movie-card {
  position: relative;
  display: flex;  height: 300px;
  overflow: hidden;
  background-color: #201133;
  border-radius: 10px;
}

.movie-card:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 1;
  background-image: linear-gradient(to left, #1f1132 55%, transparent);
  transition: 0.4s;
}

.movie-card:hover img {
  transform: scale(1.2);
}

.movie-card img {
  display: inline;
  flex: none;
  transition: ease-in-out 1000ms;
}

.description {
  padding: 40px 20px 20px;
  position: relative;
  z-index: 2;
  flex: 1 1 0px;
  min-width: 0;
  color: #fff;
}


.movie-title {
  margin-bottom: 15px;
  font-weight: bold;
  color: #fff;
  font-size: 22px;
  line-height: 1;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-decoration: none;
  min-width: 0;
}

.genres{
  display: flex;
  flex-flow: row wrap;
  gap: 4px;
  height: 44px;
  /* justify-content: flex-start; */
  align-items: flex-start;
}

.genres li {
  display: inline-block;
  padding: 3px 4px;
  list-style: none;
  font-size: 14px;
  font-weight: bold;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  color: #212529;
  background-color: #6987a6;
  color: #444;
  border-radius: 4px;
}


.link-wrap {
  position: absolute;
  bottom: 20px;
  left: 20px;
  right: 20px;
  display: inline-block;
  padding: 10px;
  text-align: center;
  background-color: #fff;
  border-radius: 10px;
  color: #555;
  font-weight: bold;
  line-height: 1;
  vertical-align: text-bottom;
}

.link-wrap,
.link-wrap:active,
.link-wrap:visited {
  text-decoration: none;
}


.movie-summary {
  margin: 10px 0;
  height: 100px;
  overflow: hidden;
  position: relative;
}

.movie-summary:before {
  content: "";
  position: absolute;
  left:0;
  right: 0;
  bottom: 0px;
  height: 30px;
  background: linear-gradient(to top, #201133 30%, transparent);
  z-index: 0;
}