.loading-page {
  display:flex;
  align-items:center;
  justify-content:center;
  height: 100vh;
  background-color:#d1d1d177;
  text-align:center;
}

.loading-page h1 {
  font-size: 60px;
  font-weight: bold;
  color : #fff;
}

.bullets{
  animation: dots 2s steps(3, end) infinite;
}



@keyframes dots {
  0%, 20% {
    color: rgba(0,0,0,0);
    text-shadow:
      .25em 0 0 rgba(0,0,0,0),
      .5em 0 0 rgba(0,0,0,0);}
  40% {
    color: white;
    text-shadow:
      .25em 0 0 rgba(0,0,0,0),
      .5em 0 0 rgba(0,0,0,0);}
  60% {
    text-shadow:
      .25em 0 0 white,
      .5em 0 0 rgba(0,0,0,0);}
  80%, 100% {
    text-shadow:
      .25em 0 0 white,
      .5em 0 0 white;}
  }