.detail-page {
  position: relative;
  display:flex;
  align-items:center;
  justify-content:center;
  height: 100vh;
  text-align:center;

}

.detail-page:before {
  content:'';
  position: absolute;
  backdrop-filter: blur(3px);
  width: 100%;
  height: 100%;
  background-image: linear-gradient(rgba(31, 17, 50, 0.5), rgba(31, 17, 50, 0.5));
}

.detail-page > div {
  padding: 20px;
  flex-basis: 40%;
  background: rgb(68 68 68 / 14%);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5.6px);
  -webkit-backdrop-filter: blur(5.6px);
  border: 1px solid rgba(255, 255, 255, 0.19);
  color: #fff;
}

.detail-page h2 {
  margin: 10px 0 8px;
  font-size: 22px;
  font-weight: bold;
}

.detail-genres{
  display: flex;
  flex-flow: row wrap;
  gap: 4px;
  justify-content: center;
  align-items: flex-start;
}

.detail-genres li {
  display: inline-block;
  padding: 3px 4px;
  list-style: none;
  font-size: 14px;
  font-weight: bold;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  color: #212529;
  background-color: #bcb8c2;
  color: #444;
  border-radius: 4px;
}